import { useGetProtectedQuery } from "../auth/slices/authApiSlice";
import { useNavigate } from "react-router-dom";
import { getFixedT } from 'i18next';
import { Image } from 'react-bootstrap';
import { Helmet } from 'react-helmet'
import 'bootstrap/dist/css/bootstrap.min.css';

import './../../assets/css/maintenance.css'
import logoPng from './../../assets/images/100g_vert.png'
import backgroundPng from './../../assets/images/signin.png'

const Maintenance = () => {
    const t = getFixedT(null, null, 'maintenance')
    const navigate = useNavigate()
    const {
        data: data,
        isLoading,
        isError
    } = useGetProtectedQuery()
    console.log('data', data)
    if (data) {
        navigate('/membre')
    }

    return (
        <>
            <Helmet title={t('title')} />
            <div class="mx-auto auth-container">
                <div class="card card0">
                    <div class="d-flex flex-lg-row flex-column-reverse">
                        <div class="card card1">
                            <form id="customerLoginForm" className="form-inner customer-login-form my-5">
                                <div class="row justify-content-center my-auto">
                                    <div class="col-md-8 col-10 my-5">
                                        <div class="row justify-content-center px-3 py-5 mb-3"> <Image id="logo" src={logoPng} /> </div>
                                        <article>
                                            <h1>{t('We&rsquo;ll be back soon!')}</h1>
                                            <div className="mt-4">
                                                <p>{t('Sorry for the inconvenience but we&rsquo;re performing some maintenance at the moment. If you need to you can always')} <a href="mailto: app@100grammes.ca">{t('contact us')}</a> {t('otherwise we&rsquo;ll be back online shortly!')}</p>
                                                <p className="mt-4">&mdash; {t('Team 100 grammes')} </p>
                                            </div>
                                        </article>

                                    </div>
                                </div>
                            </form>
                        </div>
                        <div class="card card2" style={{ backgroundImage: `url(${backgroundPng})` }}>
                        </div>

                    </div>

                </div>
            </div>



        </>
    )
}

export default Maintenance;